import React from 'react'
import { Link } from 'react-router-dom';
import { animateScroll as scroll } from 'react-scroll';

import './NavBar.css'

const NavBar = () => {
    const linkStyle = { color: "#fff", textDecoration: "none", padding: "0" };
    const toTop = () => scroll.scrollToTop()
    return (
        <nav className="nav">
            <ul>
                <Link to="/" style={linkStyle} onClick={toTop} ><li>Home</li></Link>
                <Link to="/article-list" style={linkStyle} onClick={toTop} ><li>Articles</li></Link>
                <Link to="/compose" style={linkStyle} onClick={toTop} ><li>Compose</li></Link>
                <Link to="/about" style={linkStyle} onClick={toTop} ><li>About</li></Link>

            </ul>
        </nav>
    )
}

export default NavBar
