import React, { useState } from 'react'

const CommentForm = ({ id, setarticleInfo }) => {
    const initialField = {
        username: "",
        comment: "",
    }
    const [inputField, setInputField] = useState(initialField);
    const handleChange = (e) => {
        const { name, value } = e.target;
        setInputField(prev => {
            if (name === "username") {
                return {
                    username: value,
                    comment: prev.comment,
                }
            }
            else if (name === "comment") {
                return {
                    username: prev.username,
                    comment: value,
                }
            }
        });
    }
    const handleSubmit = async (e) => {
        e.preventDefault();
        await fetch(`/api/articlelists/${id}/add-comment`, {
            method: 'post',
            body: JSON.stringify(inputField),
            headers: {
                'Content-Type': 'application/json',
            }
        }).then(res => res.json()).then(info => setarticleInfo(info)).catch(err => console.log("error :" + err));
        console.log(inputField);
        setInputField(initialField);
    }
    return (
        <form action="" className="comment-form" onSubmit={(e) => handleSubmit(e)}>
            <h4>Add comments:</h4>
            <input type="text" name="username" placeholder="username" value={inputField.username} onChange={(e) => handleChange(e)} required />
            <textarea name="comment" id="" cols="30" rows="10" placeholder="comment" value={inputField.comment} onChange={(e) => handleChange(e)} required></textarea>
            <input type="submit" value="Submit" />
        </form>


    )
}

export default CommentForm
