import React from 'react'
import { Link } from 'react-router-dom'
import './ArticleListComponent.css';
import { animateScroll as scroll } from 'react-scroll';


const ArticleListComponent = ({ articleContent, cname }) => {
    return (
        articleContent.map((item => <Link className="article-link" key={item._id} to={`/articles/${item._id}`}>
            <div className={"article " + cname} onClick={() => scroll.scrollToTop()}>
                <h3>{item.title}</h3>
                <p>"{item.content.substring(0, 100)}..."</p>
            </div>
        </Link>))
    )
}




export default ArticleListComponent
