import React from 'react'
import { BiUpvote } from 'react-icons/bi';
import './Upvote.css';
const Upvote = ({ id, upvotes, setarticleInfo }) => {
    const updateVote = async () => {
        console.log("clicked");
        await fetch(`/api/articlelists/${id}/upvote`, { method: 'post' }).then(res => res.json()).then((updatedArticleInfo) => setarticleInfo(updatedArticleInfo)).catch(err => console.log("error in upvoting: " + err));
        console.log(upvotes);
    }
    return (

        <div className="upvote-section">
            <BiUpvote className="up-icon" onClick={updateVote} />&nbsp;&nbsp;
            <span>{upvotes}</span>
        </div>

    )
}

export default Upvote
