import React, { useEffect, useState } from 'react';
import ArticleListComponent from '../../components/articlelistcomponent/ArticleListComponent';
import Notfound from '../notfound/Notfound';
import { useLocation } from 'react-router-dom'
import './Article.css'
import CommentList from '../../components/comment/CommentList';
import Upvote from '../../components/upvotes/Upvote';
import CommentForm from '../../components/CommentForm';


const Article = ({ match }) => {
    const path = useLocation().pathname;
    const id = match.params.id;

    const [articleInfo, setarticleInfo] = useState({ title: "", content: "", comments: [], upvotes: 0 });
    const [articleContent, setarticleContent] = useState([]);

    useEffect(() => {
        console.log(id);
        fetch(`/api/articlelists/${id}`).then(res => res.json()).then((info) => setarticleInfo(info)).catch(err => console.log(err));
    }, [id]);

    useEffect(() => {
        fetch(`/api/articlelists`).then(res => res.json()).then((info) => setarticleContent(info)).catch(err => console.log(err));
    }, [id]);


    if (!articleInfo) return <Notfound />
    const filteredArticleContent = articleContent.filter((item) => item._id !== id);
    return (
        <section className="article-section section-size">
            <div>
                <h1>{articleInfo.title}</h1>
                <p>{articleInfo.content}</p>
                <Upvote id={articleInfo._id} upvotes={articleInfo.upvotes} setarticleInfo={setarticleInfo} />
            </div>
            <h4>comments:</h4>
            <CommentList commentInfo={articleInfo.comments} />
            <CommentForm id={articleInfo._id} setarticleInfo={setarticleInfo} />
            <h4>Related Articles:</h4>
            <div className="related">
                <ArticleListComponent articleContent={filteredArticleContent} cname={path === `/articles/${id}` ? "related-article-width" : ""} />
            </div>
        </section>
    )
}

export default Article
