import React from 'react'
import './CommentList.css';
const CommentList = ({ commentInfo }) => {

    return (
        <>
            {
                commentInfo.map((item => {
                    return <div className="comment-list" key={item._id}>
                        <h5>{item.username}</h5>
                        <p>{item.comment}</p>
                        <hr />
                    </div>
                }))

            }
        </>
    )


}

export default CommentList
