import React, { useEffect, useState } from 'react'
import ArticleListComponent from '../../components/articlelistcomponent/ArticleListComponent';
import './ArticleList.css';
const ArticleList = () => {
    const [data, setData] = useState([]);
    useEffect(() => {
        fetch("api/articlelists").then(res => res.json()).then((info) => setData(info)).catch(err => console.log(err));
    }, []);
    console.log(data);
    return (
        <section className="article-list-section section-size">
            <h2>Articles:</h2>
            <ArticleListComponent articleContent={data} />
        </section>
    )
}

export default ArticleList

