import React, { useState } from 'react'
import './Compose.css';
const Compose = () => {
    const initialField = {
        auther: "",
        title: "",
        content: "",
    }
    const [inputField, setInputField] = useState(initialField);
    const handleChange = (e) => {
        const { name, value } = e.target;
        setInputField(prev => {
            if (name === "auther") {
                return {
                    auther: value,
                    title: prev.title,
                    content: prev.content,
                }
            }
            else if (name === "title") {
                return {
                    auther: prev.auther,
                    title: value,
                    content: prev.content,
                }
            }
            else if (name === "content") {
                return {
                    auther: prev.auther,
                    title: prev.title,
                    content: value,
                }
            }
        });
    }
    const handleSubmit = async (e) => {
        e.preventDefault();
        await fetch("/api/compose", {
            method: 'post',
            body: JSON.stringify(inputField),
            headers: {
                'Content-Type': 'application/json',
            }
        }).then(res => res.json()).then(info => console.log(info)).catch(err => console.log("error :" + err));
        alert("Article has submited successfully");
        setInputField(initialField);

    }
    return (
        <section className="compose-section section-size">

            <form className="compose-form" action="" method="post" onSubmit={(e) => handleSubmit(e)}>
                <h1>Compose Your Own Article</h1>
                <input type="text" name="auther" id="" placeholder="Auther" value={inputField.auther} onChange={(e) => handleChange(e)} required />
                <input type="text" name="title" id="" placeholder="Title" value={inputField.title} onChange={(e) => handleChange(e)} required />
                <textarea name="content" id="" cols="80" rows="10" placeholder="content" value={inputField.content} onChange={(e) => handleChange(e)} required></textarea>
                <input type="submit" value="Submit" />
            </form>

        </section>
    )
}

export default Compose
